.heading {
  font-size: 48px;
  line-height: 1;
  position: absolute;
  text-transform: uppercase;
  font-weight: 300;
  width: max-content;
}

.heading::before,
.heading::after {
  content: "" ;
  display: block;
  position: absolute;
  height: 2px;
  top: calc(100% + 5px);
  width: calc(50% - 20px);
  background-color: $highlight-color;
}

.heading::before {
  left: 0;
}

.heading::after {
  right: 0;
}

.heading__crown {
  display: block;
  position: absolute;
  left: 0;
  right: 0;
  width: 20px;
  height: 13px;
  top: calc(100% + 4px);
  margin: auto;
  background-image: url("..//images/crown.svg");
}
